// eslint-disable-next-line
// @ts-nocheck

import { useCallback, useRef, useState, MouseEvent, TouchEvent } from 'react';

interface LongPressOptions {
  shouldPreventDefault?: boolean;
  delay?: number;
}

export const useLongPress = (
  onLongPress: (event: MouseEvent<HTMLElement> | TouchEvent<HTMLElement>) => void,
  onClick: () => void,
  { shouldPreventDefault = true, delay = 300 }: LongPressOptions = {},
) => {
  const [longPressTriggered, setLongPressTriggered] = useState<boolean>(false);
  const timeout = useRef<ReturnType<typeof setTimeout> | null>(null);
  const target = useRef<HTMLElement | null>(null);

  const start = useCallback(
    (event: MouseEvent<HTMLElement> | TouchEvent<HTMLElement>) => {
      if (shouldPreventDefault && event.target) {
        (event.target as HTMLElement).addEventListener('touchend', preventDefault, {
          passive: false,
        });
        target.current = event.target as HTMLElement;
      }
      timeout.current = setTimeout(() => {
        onLongPress(event);
        setLongPressTriggered(true);
      }, delay);
    },
    [onLongPress, delay, shouldPreventDefault],
  );

  const clear = useCallback(
    (event: MouseEvent<HTMLElement> | TouchEvent<HTMLElement>, shouldTriggerClick = true) => {
      if (timeout.current) {
        clearTimeout(timeout.current);
      }
      if (shouldTriggerClick && !longPressTriggered && onClick) {
        onClick();
      }
      setLongPressTriggered(false);
      if (shouldPreventDefault && target.current) {
        target.current.removeEventListener('touchend', preventDefault);
      }
    },
    [shouldPreventDefault, onClick, longPressTriggered],
  );

  return {
    onMouseDown: (e: MouseEvent<HTMLElement>) => start(e),
    onTouchStart: (e: TouchEvent<HTMLElement>) => start(e),
    onMouseUp: (e: MouseEvent<HTMLElement>) => clear(e),
    onMouseLeave: (e: MouseEvent<HTMLElement>) => clear(e, false),
    onTouchEnd: (e: TouchEvent<HTMLElement>) => clear(e),
  };
};

const isTouchEvent = (event: MouseEvent | TouchEvent): event is TouchEvent => {
  return 'touches' in event;
};

const preventDefault = (event: TouchEvent<HTMLElement>) => {
  if (!isTouchEvent(event)) return;

  if (event.touches.length < 2 && event.preventDefault) {
    event.preventDefault();
  }
};
