import { usePortalRef } from '@hooks';
import { useCustomiserStore } from '@store/customiser';
import cn from 'classnames';
import ReactDOM from 'react-dom';

import styles from './NavButtons.module.scss';
import { Falsey } from 'lodash';

import DontShowAgain from '../../ui/DontShowAgain';
import { useState } from 'react';
import ContainedButton from '@components/ui/Button/ContainedButton';
import { finished } from 'stream';
import { useDesignStore } from '@store/design';

export interface NavButtonsProps {
  className?: string;
  confirmText?: string | Falsey;
  confirmStorage?: string;
  onNext?: (() => void) | Falsey;
  finishedNavs?: (
    | {
        name: string;
        isFinished: boolean;
      }
    | undefined
  )[];
}

const NavButtons = ({
  finishedNavs: finishedNavsParam,
  className,
  confirmText,
  confirmStorage,
  onNext,
}: NavButtonsProps) => {
  const rootClassName = cn(styles.root, className);

  const {
    navItems,
    selectedNav,
    parts,
    sizing,

    cancelPartChange,
    resetNav,
    setSelectedNav,
    designComplete,
  } = useCustomiserStore();

  const { setResetCamera, addingToCart } = useDesignStore();

  const navIndex = navItems.findIndex((nav) => nav.name === selectedNav?.name);
  const hasNextTab = navIndex < navItems.length - 1;

  const portalRef = usePortalRef('CustomiserNavActions');

  const popupPortalRef = usePortalRef('popupContainer');
  const [showPopup, setShowPopup] = useState<boolean>(false);

  const notifTitle = confirmStorage;
  const localStorageItem = localStorage.getItem(notifTitle || '');
  const shouldShowNotification = !localStorageItem;

  let finishedNavs = parts.map((part) => {
    const partName = part.part.name;
    if (!partName) return;
    const isFinishedPart = !!navItems.find((navItem) => {
      return navItem.name === partName;
    });
    return {
      name: partName,
      isFinished: isFinishedPart,
    };
  });

  if (finishedNavsParam && finishedNavsParam.length) {
    finishedNavs = [...finishedNavs, ...finishedNavsParam];
  }

  let isFinishedCurrentNav = !!(
    selectedNav && finishedNavs.find((nav) => nav?.name === selectedNav.name)?.isFinished
  );
  let isRequired = selectedNav?.required;
  if (selectedNav?.name === 'Fitting') {
    isRequired = false;
  } else if (selectedNav?.name === 'Size') {
    isFinishedCurrentNav = !!(sizing?.height?.value && sizing?.weight?.value);
  }

  const noCancelNavs = ['Fitting', 'Text', 'Size'];
  const shouldHideCancel = !!noCancelNavs.find((noCancelNav) => noCancelNav === selectedNav?.name);

  const saveHandler = () => {
    if (!isRequired || (navIndex !== -1 && isFinishedCurrentNav)) {
      setSelectedNav(navIndex + 1);
    }

    // if (!confirmText) {
    //   resetNav();
    // } else {
    //   if (shouldShowNotification) {
    //     setShowPopup(true);
    //   } else {
    //     resetNav();
    //   }
    // }
  };

  const isClickableAddToBasket = isFinishedCurrentNav && !addingToCart && designComplete();
  const addToBasketHandler = () => {
    setResetCamera();
  };

  if (!portalRef || !popupPortalRef) {
    return null;
  }

  return ReactDOM.createPortal(
    <>
      <div className={rootClassName}>
        <ContainedButton
          isClickable={isFinishedCurrentNav}
          isHidden={shouldHideCancel}
          onClick={() => cancelPartChange()}
        >
          Cancel
        </ContainedButton>
        {!onNext ? (
          hasNextTab ? (
            <ContainedButton
              isClickable={!isRequired || isFinishedCurrentNav}
              onClick={saveHandler}
            >
              Save
            </ContainedButton>
          ) : (
            <ContainedButton
              color={isClickableAddToBasket ? 'green' : ''}
              isClickable={isClickableAddToBasket}
              onClick={addToBasketHandler}
            >
              {addingToCart ? 'Saving...' : 'Add to basket'}
            </ContainedButton>
          )
        ) : (
          <ContainedButton onClick={onNext}>Next</ContainedButton>
        )}
      </div>
      {confirmText &&
        showPopup &&
        shouldShowNotification &&
        notifTitle &&
        ReactDOM.createPortal(
          <DontShowAgain cb={resetNav} setShowNotification={setShowPopup} notifName={notifTitle}>
            <span>{confirmText}</span>
          </DontShowAgain>,
          popupPortalRef,
        )}
    </>,
    portalRef,
  );
};

export default NavButtons;
