import { CustomProductOptionFragment } from '@graphql/generated/graphql';
import { useCustomiserStore } from '@store/customiser';
import { motion } from 'framer-motion';
import NavButtons from '../NavButtons';
import styles from './NavFitting.module.scss';
import CapsuleButton from '@components/ui/Button/CapsuleNavButton';
import { useEffect, useState } from 'react';
import ContainedNavButton from '@components/ui/Button/ContainedNavButton';

export interface FittingOptionProps {
  option: CustomProductOptionFragment;
}

const FittingOption = ({ option }: FittingOptionProps) => {
  const selectedModels = useCustomiserStore((state) => state.selectedModels);
  const setSelectedModel = useCustomiserStore((state) => state.setSelectedModel);

  if (!option) {
    return null;
  }

  const getSelected = selectedModels.find((m) => m.optionId === option.id)?.model;

  return (
    <>
      <motion.div
        className={styles.fittingOption}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <div className={styles.fittingButtons}>
          {option.models?.map((m) => (
            <ContainedNavButton
              key={m?.id}
              isSelected={getSelected?.id === m?.model?.data?.id}
              classNames={styles.fittingButton}
              onClick={() => {
                setSelectedModel(option.id, m?.model?.data);
              }}
            >
              {m?.model?.data?.attributes?.name}
            </ContainedNavButton>
          ))}
        </div>
      </motion.div>
    </>
  );
};

interface NavFittingProps {
  additionalFinishedNavs?: {
    name: string;
    isFinished: boolean;
  }[];
  setAdditionalFinishedNavs?: React.Dispatch<
    React.SetStateAction<{ name: string; isFinished: boolean }[]>
  >;
  finishedNavs: ({ name: string; isFinished: boolean } | undefined)[];
  setFinishedNavs: React.Dispatch<
    React.SetStateAction<({ name: string; isFinished: boolean } | undefined)[]>
  >;
}
const NavFitting = ({ finishedNavs, setFinishedNavs }: NavFittingProps) => {
  const fittingOptions = useCustomiserStore(
    (state) => state.customProduct?.attributes?.options,
  ) as Array<CustomProductOptionFragment>;
  const { setCameraFocus } = useCustomiserStore();

  const fittingOptionsTitles = fittingOptions?.map((o) => o.name);

  const initialStep = finishedNavs?.find(
    (navItem) => navItem?.name === 'Fitting' && navItem.isFinished === true,
  )
    ? fittingOptions.length - 1
    : 0;
  const [navStep, setNavStep] = useState(initialStep);
  const hasNextStep = navStep < fittingOptions.length - 1;

  useEffect(() => {
    if (!hasNextStep) {
      setFinishedNavs &&
        setFinishedNavs((prev) => {
          const iFitting = prev.findIndex((navItem) => navItem?.name === 'Fitting');
          if (iFitting !== -1) {
            prev[iFitting] = {
              name: 'Fitting',
              isFinished: true,
            };
            return prev;
          }
          const newFinishedNavs = [
            ...prev,
            {
              name: 'Fitting',
              isFinished: true,
            },
          ];
          return newFinishedNavs;
        });
    }
  }, [navStep]);

  return (
    <div className={styles.root}>
      <div className={styles.navButtonContainer}>
        {fittingOptionsTitles.map((title, i) => {
          return (
            <CapsuleButton
              isFinished={navStep > i}
              isSelected={navStep === i}
              onClick={() => {
                setNavStep(i);
                setCameraFocus(title || '');
              }}
              key={i}
            >
              {title}
            </CapsuleButton>
          );
        })}
      </div>
      <div>
        <b>Select {fittingOptionsTitles[navStep]}:</b>
      </div>
      <br />
      {<FittingOption key={fittingOptions[navStep]?.id} option={fittingOptions[navStep]} />}
      <NavButtons
        onNext={
          hasNextStep &&
          (() => {
            setNavStep((cur) => {
              const newCur = cur + 1;
              setCameraFocus(fittingOptionsTitles[newCur] || '');
              return newCur;
            });
          })
        }
        finishedNavs={finishedNavs}
      />
    </div>
  );
};

export default NavFitting;
